.product-card {
    display: flex;
    flex-direction: column;
  
    padding: 2%;
    flex: 1 16%;
  
    background-color: #fff;
    box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.25);
  }

  .product-image img {
    width: 100%;
  }
  
  .product-info {
    margin-top: auto;
    padding-top: 20px;
    text-align: center;
  }

  h5 {
    font-weight: 500;
    line-height: 1.7em;
  }
  
  h6 {
    color: #666;
    font-size: 14px;
  }

  .qtySelect {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .qtySelect > span{
    width: 50px;
    text-align: center;
  }

  .add-to-cart {
    display: flex;
    flex-direction: column;
  }